<template>
  <div>
    <div class="btn">
      <el-button type="primary" @click="addSchool">添加学校</el-button>
    </div>

    <el-dialog title="添加学校" :visible.sync="dialogSchool" :close-on-click-modal="false">
      <div class="box">
        <el-form :model="form" label-width="100px">
          <el-form-item label="学校名称">
            <el-input v-model="form.name" placeholder="请输入学校名称"></el-input>
          </el-form-item>
          <el-form-item label="学校地址">
            <el-input type="textarea" v-model="form.address" placeholder="请输入学校地址"></el-input>
          </el-form-item>
          <el-form-item label="人脸库(进)">
            <el-input v-model="form.in_group" placeholder="请输入人脸库(进)"></el-input>
          </el-form-item>
          <el-form-item label="人脸库(出)">
            <el-input v-model="form.out_group" placeholder="请输入人脸库(出)"></el-input>
          </el-form-item>
          <el-form-item label="收费模式">
            <el-input v-model="form.mode"></el-input>
          </el-form-item>
          <!-- 	<el-form-item label="用户名">
						<el-input v-model="form.user.username"></el-input>
					</el-form-item>
					<el-form-item label="密码">
						<el-input v-model="form.user.password"></el-input>
          </el-form-item>-->
          <div v-for="(item,index) in form.grades" :key="index">
            <el-form-item label="年级选择">
              <el-select v-model="item.title" placeholder="请选择年级">
                <el-option
                  v-for="selectItem in grade"
                  :key="selectItem.value"
                  :label="selectItem.label"
                  :value="selectItem.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="班级">
              <el-transfer
                v-model="item.classes"
                :data="classList"
                :titles="['班级', '选中班级']"
                :button-texts="['取消', '确定']"
              ></el-transfer>
            </el-form-item>
          </div>
          <el-form-item label="操作">
            <el-button type="success" @click="addGrade">添加年级</el-button>
            <el-button type="success" @click="delGrade">删除年级</el-button>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="newSchool">提交</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>

    <el-table :data="tableDate">
      <el-table-column prop="id" label="学校ID" align="center"></el-table-column>
      <el-table-column prop="name" label="学校名称" align="center"></el-table-column>
      <el-table-column prop="address" label="学校地址" align="center"></el-table-column>
      <el-table-column prop="created_at" label="创建时间" align="center"></el-table-column>
      <el-table-column label="操作" align="center" width="400px">
        <template slot-scope="scope">
          <el-button size="mini" type="success" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleShowUser(scope.$index, scope.row)"
          >查看用户</el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleShowFinancial(scope.$index, scope.row)"
          >查看财务</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除学校</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="查看学校所有" :visible.sync="dialogUser" width="80%" @close="closeShowUser">
      <div class="box">
        <div class="btn">
          <el-select v-model="user" @change="handleUser" placeholder="请选择">
            <el-option
              v-for="item in userList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>

        <template v-if="user == '用户'">
          <el-table :data="userData">
            <el-table-column prop="id" label="用户ID" align="center"></el-table-column>
            <el-table-column prop="href" label="头像" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.href" alt style="max-width:180px;max-height:80px;" />
              </template>
            </el-table-column>
            <el-table-column prop="name" label="姓名" align="center"></el-table-column>
            <el-table-column prop="sex" label="性别" align="center">
              <template slot-scope="scope">
                <span v-text="scope.row.sex == 1 ? '男' : '女' "></span>
              </template>
            </el-table-column>
            <el-table-column prop="phone" label="手机号码" align="center"></el-table-column>
            <el-table-column prop="worker" label="是否老师" align="center"></el-table-column>
            <el-table-column prop="worker" label="是否班主任" align="center"></el-table-column>
            <el-table-column prop="worker" label="是否教职工" align="center"></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleUserChange"
              :current-page.sync="currentUserPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="userPageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="userTotalPage"
              @size-change="handleUserSizeChange"
            ></el-pagination>
          </div>
        </template>
        <template v-if="user == '学生'">
          <el-table :data="studentData">
            <el-table-column prop="id" label="学号" align="center"></el-table-column>
            <el-table-column prop="name" label="姓名" align="center"></el-table-column>
            <el-table-column prop="sex" label="性别" align="center">
              <template slot-scope="scope">
                <span v-text="scope.row.sex == 1 ? '男' : '女' "></span>
              </template>
            </el-table-column>
            <el-table-column prop="age" label="年龄" align="center"></el-table-column>
            <el-table-column prop="grades.title" label="年级" align="center"></el-table-column>
            <el-table-column prop="class.title" label="班级" align="center"></el-table-column>
            <el-table-column prop="face_id" label="生活头像" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.face_id" alt style="max-width:180px;max-height:80px;" />
              </template>
            </el-table-column>
            <el-table-column prop="face_image" label="人脸头像" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.face_image" alt style="max-width:180px;max-height:80px;" />
              </template>
            </el-table-column>
            <el-table-column prop="userInfo.user_id" label="默认家长ID" align="center"></el-table-column>
            <el-table-column prop="userInfo.name" label="默认家长姓名" align="center"></el-table-column>
            <el-table-column prop="userInfo.phone" label="默认家长手机" align="center"></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleStudentChange"
              :current-page.sync="currentStudentPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="studentPageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="studentTotalPage"
              @size-change="handleStudentSizeChange"
            ></el-pagination>
          </div>
        </template>
        <template v-if="user == '教师'">
          <el-table :data="teacherData">
            <el-table-column prop="id" label="用户ID" align="center"></el-table-column>
            <el-table-column prop="name" label="姓名" align="center"></el-table-column>
            <el-table-column prop="wxUser.nickname" label="昵称" align="center"></el-table-column>
            <el-table-column prop="sex" label="性别" align="center">
              <template slot-scope="scope">
                <span v-text="scope.row.sex == 1 ? '男' : '女' "></span>
              </template>
            </el-table-column>
            <el-table-column prop="phone" label="手机号码" align="center"></el-table-column>

            <el-table-column prop="grades.title" label="年级" align="center">
              <template slot-scope="scope">
                <span v-text="scope.row.grades.title == null ? '无' : scope.row.grades.title "></span>
              </template>
            </el-table-column>
            <el-table-column prop="class.title" label="班级" align="center">
              <template slot-scope="scope">
                <span v-text="scope.row.class.title == null ? '无' : scope.row.class.title "></span>
              </template>
            </el-table-column>
            <el-table-column prop="href" label="人脸头像" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.href" alt style="max-width:180px;max-height:80px;" />
              </template>
            </el-table-column>
            <el-table-column prop="worker" label="是否是班主任" align="center"></el-table-column>
            <el-table-column prop="state" label="待审核" align="center"></el-table-column>
            <el-table-column prop="created_at" label="创建时间" align="center"></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleTeacherChange"
              :current-page.sync="currentTeacherPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="teacherPageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="teacherTotalPage"
              @size-change="handleTeacherSizeChange"
            ></el-pagination>
          </div>
        </template>

        <template v-if="user == '访客'">
          <el-table :data="visitorData">
            <el-table-column prop="id" label="访客ID" align="center"></el-table-column>
            <el-table-column prop="user_id" label="用户ID" align="center"></el-table-column>
            <el-table-column prop="name" label="姓名" align="center"></el-table-column>
            <el-table-column prop="created_at" label="手机号" align="center"></el-table-column>
            <el-table-column prop="user_name" label="拜访人" align="center"></el-table-column>
            <el-table-column prop="reason" label="拜访理由" align="center"></el-table-column>
            <el-table-column prop="href" label="访客人脸" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.href" alt style="max-width:180px;max-height:80px;" />
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleVisitorChange"
              :current-page.sync="currentVisitorPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="visitorPageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="visitorTotalPage"
              @size-change="handleVisitorSizeChange"
            ></el-pagination>
          </div>
        </template>
      </div>
    </el-dialog>
    <el-dialog title="查看财务" :visible.sync="dialogFinancial" width="80%" @close="closeFinancial">
      <div class="box">
        <div class="btn">
          <el-select v-model="financial" @change="handleFinancial" placeholder="请选择">
            <el-option
              v-for="item in financialList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>

        <div v-if="financial === '订单列表'">
          <el-table :data="orderData">
            <el-table-column prop="school_id" label="学校ID" align="center"></el-table-column>
            <el-table-column prop="no" label="订单ID" align="center"></el-table-column>
            <el-table-column prop="user_id" label="用户ID" align="center"></el-table-column>
            <el-table-column prop="user_name" label="用户名" align="center"></el-table-column>
            <el-table-column prop="name" label="商品名称" align="center"></el-table-column>
            <el-table-column prop="price" label="商品价格" align="center"></el-table-column>
            <el-table-column prop="time" label="商品时长" align="center"></el-table-column>
            <el-table-column prop="status" label="订单状态" align="center"></el-table-column>
            <el-table-column prop="created_id" label="创建时间" align="center"></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleOrderChange"
              :current-page.sync="currentOrderPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="orderPageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="orderTotalPage"
              @size-change="handleOrderSizeChange"
            ></el-pagination>
          </div>
        </div>
        <div v-if="financial === '财务统计'">
          <el-table :data="financialData">
            <el-table-column prop="id" label="周总额" align="center"></el-table-column>
            <el-table-column prop="name" label="月总额" align="center"></el-table-column>
            <el-table-column prop="address" label="季度总额" align="center"></el-table-column>
            <el-table-column prop="created_at" label="上一季度总额" align="center"></el-table-column>
            <el-table-column prop="id" label="年总额" align="center"></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleFinanceChange"
              :current-page.sync="currentFinancePage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="financePageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="financeTotalPage"
              @size-change="handleFinanceSizeChange"
            ></el-pagination>
          </div>
        </div>
      </div>
    </el-dialog>

    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="10"
        layout="sizes, prev, pager, next, jumper"
        :total="totalPage"
        @size-change="handleSizeChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import API from "@/api/index.js";

export default {
  name: "school",
  data() {
    return {
      // 添加学校
      dialogSchool: false,
      form: {
        name: "",
        address: "",
        in_group: "",
        out_group: "",
        mode: 1,
        user: {
          username: "",
          password: ""
        },
        grades: [],
        id: ""
      },
      grade: [
        {
          value: "一年级",
          label: "一年级"
        },
        {
          value: "二年级",
          label: "二年级"
        },
        {
          value: "三年级",
          label: "三年级"
        },
        {
          value: "四年级",
          label: "四年级"
        },
        {
          value: "五年级",
          label: "五年级"
        },
        {
          value: "六年级",
          label: "六年级"
        },
        {
          value: "初一",
          label: "初一"
        },
        {
          value: "初二",
          label: "初二"
        },
        {
          value: "初三",
          label: "初三"
        }
      ],
      classList: [],

      // 操作
      // 查看用户
      dialogUser: false,
      user: "访客",
      userList: [
        {
          value: 1,
          label: "用户"
        },
        {
          value: 2,
          label: "学生"
        },
        {
          value: 3,
          label: "教师"
        },
        {
          value: 4,
          label: "访客"
        }
      ],
      id: "",
      userData: [], // 用户列表
      userPageSize: 10,
      userTotalPage: 0,
      currentUserPage: 1,

      teacherData: [], // 教师列表
      teacherPageSize: 10,
      teacherTotalPage: 0,
      currentTeacherPage: 1,

      studentData: [], // 学生列表
      studentPageSize: 10,
      studentTotalPage: 0,
      currentStudentPage: 1,

      visitorData: [], // 访客列表
      visitorPageSize: 10,
      visitorTotalPage: 0,
      currentVisitorPage: 1,

      // 查看财务
      dialogFinancial: false,
      financial: "订单列表",
      financialList: [
        {
          value: 1,
          label: "订单列表"
        },
        {
          value: 2,
          label: "财务统计"
        }
      ],
      orderData: [],
      orderPageSize: 10, // 订单列表
      orderTotalPage: 0,
      currentOrderPage: 1,
      financialData: [],
      financePageSize: 10, // 财务统计
      financeTotalPage: 0,
      currentFinancePage: 1,

      tableDate: [],

      // 分页
      currentPage: 1,
      totalPage: 0
    };
  },
  mounted() {
    this.generateData();
    this.getSchool();
  },
  methods: {
    // 获取学校列表
    getSchool() {
      var self = this;
      API.schools(self.currentPage).then(res => {
        self.tableDate = res.data;
        self.totalPage = res.total;
      });
    },
    newSchool() {
      var self = this;
      self.form.grades.forEach(item => {
        for (var i = 0; i < item.classes.length; i++) {
          item.classes[i] = {
            title: item.classes[i]
          };
        }
      });
      API.school(self.form).then(res => {
        self.dialogSchool = false;
        self.getSchool();
        self.form = {
          name: "",
          address: "",
          in_group: "",
          out_group: "",
          mode: 1,
          user: {
            username: "",
            password: ""
          },
          grades: []
        };
        self.$message.success("提交成功");
        self.currentPage = 1;
      });
    },
    // 操作
    handleEdit(index, row) {
      var self = this;
      self.dialogSchool = true;
      self.form = {
        name: row.name,
        address: row.address,
        in_group: row.in_group,
        out_group: row.out_group,
        mode: row.mode,
        user: {
          username: "",
          password: ""
        },
        grades: [],
        id: row.id
      };
    },
    getState() {
      var self = this;
      self.teacherData.forEach(item => {
        switch (item.state) {
          case 1:
            item.state = "待审核";
            break;
          case 2:
            item.state = "已通过";
            break;
          case 3:
            item.state = "未通过";
        }

        switch (item.worker) {
          case 0:
            item.worker = "否";
            break;
          case 1:
            item.worker = "否";
            break;
          case 2:
            item.worker = "是";
            break;
          case 3:
            item.worker = "否";
        }
      });

      self.userData.forEach(item => {
        switch (item.state) {
          case 1:
            item.state = "待审核";
            break;
          case 2:
            item.state = "已通过";
            break;
          case 3:
            item.state = "未通过";
        }

        switch (item.worker) {
          case 0:
            item.worker = "否";
            break;
          case 1:
            item.worker = "否";
            break;
          case 2:
            item.worker = "是";
            break;
          case 3:
            item.worker = "否";
        }
      });
    },
    handleShowUser(index, row) {
      var self = this;
      self.dialogUser = true;
      self.id = row.id;
    },
    closeShowUser() {
      var self = this;
      self.user = "访客";
      self.userData = [];
      self.studentData = [];
      self.teacherData = [];
      self.visitorData = [];
    },
    handleUser(value) {
      var self = this;
      switch (value) {
        case 1:
          self.$nextTick(() => {
            self.user = "用户";
            API.schoolUser(1, 10, self.id).then(res => {
              self.userData = res.data;
              self.userTotalPage = res.total;
              self.$message.success("获取数据成功");
              self.getState();
            });
          });
          break;
        case 2:
          self.$nextTick(() => {
            self.user = "学生";
            API.studentInfo(1, 10, self.id).then(res => {
              self.studentData = res.data;
              self.studentTotalPage = res.total;
              self.$message.success("获取数据成功");
            });
          });
          break;
        case 3:
          self.$nextTick(() => {
            self.user = "教师";
            API.teacherUser(1, 10, self.id).then(res => {
              self.teacherData = res.data;
              self.teacherTotalPage = res.total;
              self.getState();
              self.$message.success("获取数据成功");
            });
          });
          break;
        case 4:
          self.$nextTick(() => {
            self.user = "访客";
            API.visitors(1, 10, self.id).then(res => {
              self.visitorData = res.data;
              self.visitorTotalPage = res.total;
              self.$message.success("获取数据成功");
            });
          });
          break;
      }
    },
    handleShowFinancial(index, row) {
      var self = this;
      self.dialogFinancial = true;
      self.id = row.id;
    },
    handleFinancial(value) {
      var self = this;
      switch (value) {
        case 1:
          self.$nextTick(() => {
            self.financial = "订单列表";
            API.orders(1, 10, self.id).then(res => {
              self.orderData = res.data;
              self.orderTotalPage = res.total;
              self.orderData.forEach(item => {
                switch (item.status) {
                  case 1:
                    item.status = "提交";
                    break;
                  case 2:
                    item.status = "已付款";
                    break;
                  case 3:
                    item.status = "无效";
                }
              });
              self.$message.success("获取数据成功");
            });
          });
          break;
        case 2:
          self.financial = "财务统计";
          API.orders(1, 10, self.id).then(res => {
            self.financialData = res.data;
            self.financeTotalPage = res.total;
            self.$message.success("获取数据成功");
          });
          break;
      }
    },
    closeFinancial() {
      var self = this;
      self.financial = "订单列表";
      self.orderData = [];
      self.financialData = [];
    },
    handleDelete(index, row) {},

    // 添加学校
    addSchool() {
      var self = this;
      self.dialogSchool = true;
      self.form = {
        name: "",
        address: "",
        in_group: "",
        out_group: "",
        mode: 1,
        user: {
          username: "",
          password: ""
        },
        grades: []
      };
    },
    // 年级操作
    addGrade() {
      var self = this;
      self.form.grades.push({});
    },
    delGrade() {
      var self = this;
      self.form.grades.pop({});
    },
    generateData(_) {
      var self = this;
      var classData = [
        "一班",
        "二班",
        "三班",
        "四班",
        "五班",
        "六班",
        "七班",
        "八班",
        "九班",
        "十班"
      ];
      // var classData = ['小班', '1班','2班','3班','4班','5班']
      for (let i = 0; i < classData.length; i++) {
        self.classList.push({
          key: classData[i],
          label: classData[i]
        });
      }
      return self.classList;
    },

